.icon {

  width: 24px;
  height: 24px;

  &--close {
    background-image: url(../images/icon/icon-close.svg);
    background-color: #fff;
    border-radius: 50%;
  }

  &--menu {
    background-image: url(../images/icon/menu-icon.svg);

    @include onSmallDesktop {
      display: none;
    }
  }
}
