.slider {

  &__menu {
    position: absolute;
    left: 20px;
    bottom: 24px;

    @include flexbox (flex, column);
    gap: 16px;

    @include onTablet {
      left: 41px;
      bottom: 32px;
    }

    @include onSmallDesktop {
      flex-direction: row;
      gap: 24px;
      left: 78px;
      bottom: 48px;
    }

    @include onDesktop {
      left: 48px;
      bottom: 48px;
    }
  }

  &__button-container {
    @include flexbox (flex);
    gap: 16px;
  }

  &__button {
    cursor: pointer;
    border: #fff;
    width: 32px;
    height: 32px;
    border-radius: 50%;
    background-position: center;

    &:hover {
      box-shadow: rgba(
        0, 0, 0, 0.16) 0 1px 4px,
        rgb(51, 51, 51) 0 0 0 3px;
    }
  }

  &__button--left {
    background-image: url(../images/sub-title/arrow-left.svg);
  }

  &__button--right {
    background-image: url(../images/sub-title/arrow-right.svg);
  }

  &__title {
    padding-bottom: 8px;
    font-family: $first-family;
    font-style: normal;
    font-weight: 700;
    font-size: 13px;
    line-height: 20px;

    letter-spacing: 3px;
    text-transform: uppercase;

    color: $main-text-color;
  }

  &__text {
    font-family: $second-family;
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 140%;

    color: $main-text-color;
  }
}
